* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  margin: 0 auto;
  align-self: center;
  background: #fff;
  color: #cc9900;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal; }

form {
  width: 320px;
  float: left;
  margin: 20px; }
  form > div {
    position: relative;
    overflow: hidden; }
  form input {
    width: 100%;
    border: 3px solid gray;
    background: none;
    position: relative;
    top: 0;
    left: 0;
    z-index: 1;
    padding: 8px 12px;
    outline: 0; }
    form input:valid {
      background: white; }
    form input:focus {
      border-color: #f06d06; }
    form input:focus + label {
      background: #f06d06;
      color: white;
      font-size: 70%;
      padding: 2px 6px;
      z-index: 1;
      text-transform: uppercase; }
  form .imePolja {
    transition: background 0.2s, color 0.2s, top 0.2s, bottom 0.2s, right 0.2s, left 0.2s;
    position: absolute;
    color: #a80000;
    padding: 1px 6px; }

form.white input {
  padding: 12px 12px 12px 12px; }

form.white label {
  top: 0;
  bottom: -5px;
  left: 0;
  width: 100%; }

form.white input:focus,
form.white textarea:focus {
  padding: 4px 6px 20px 6px; }

form.white input:focus + label {
  top: 100%;
  margin-top: -16px; }

.myButton {
  background-color: rgba(190, 156, 0, 0.897);
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer; }
