@import "../../../index.scss";

form {
  width: 320px;
  float: left;
  margin: 20px;
  > div {
    position: relative;
    overflow: hidden;
  }
  input {
    width: 100%;
    border: 3px solid gray;
    background: none;
    position: relative;
    top: 0;
    left: 0;
    z-index: 1;
    padding: 8px 12px;
    outline: 0;
    &:valid {
      background: white;
    }
    &:focus {
      border-color: #f06d06;
    }
    &:focus + label {
      background: #f06d06;
      color: white;
      font-size: 70%;
      padding: 2px 6px;
      z-index: 1;
      text-transform: uppercase;
    }
  }
  .imePolja {
    transition: background 0.2s, color 0.2s, top 0.2s, bottom 0.2s, right 0.2s,
      left 0.2s;
    position: absolute;
    color: rgb(168, 0, 0);
    padding: 1px 6px;
  }
}

form.white {
  input {
    padding: 12px 12px 12px 12px;
  }
  label {
    top: 0;
    bottom: -5px;
    left: 0;
    width: 100%;
  }
  input:focus,
  textarea:focus {
    padding: 4px 6px 20px 6px;
  }
  input:focus + label {
    top: 100%;
    margin-top: -16px;
  }
}

